*{
    padding:0;
    margin:0;
}

.container{
    height:100vh;
    position:fixed;
    width:1000px;
}

.container .sidebar{
    position:fixed;
    height:1vh;
    width:90px;
    z-index:10;
    text-align:center;
    box-shadow:0px 15px 30px rgba(0,0,0,0.1);
}
.sidebar li:nth-child(1){
    padding:30px 0px;
}


.sidebar li:hover:nth-child(1){
    padding:30px 0px;
    background-color:transparent;
    color:#000;
}

.sidebar li{
    padding:12px;
    cursor:pointer;
    transition:all 0.5s;
    letter-spacing:1px;
    display:flex;
    flex-direction:column;
}

.sidebar li span:nth-child(1){
    font-size:20px;
}

.sidebar li span:nth-child(2){
    font-size:10px;
}

.sidebar li:hover{
    background-color:#f2f2f2;
    padding:12px;
     
}

.container .content{
    
    padding-left:75px;
}